@import url(https://fonts.googleapis.com/css2?family=Cormorant+Upright:wght@400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant+Upright:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: 'Cormorant Upright', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
menu,
nav,
output,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
}

.img-container {
  position: relative;
  width: 28rem;
  height: 28rem;
  margin: auto;
  display: block;
  overflow: hidden;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.card-container {
  flex: 1 1 20rem;
  margin: 2rem 1rem;
}
.card-content h1 {
  text-transform: uppercase;
  font-size: 2rem;
}
.card-content h2 {
  text-transform: uppercase;
  font-size: 1rem;
}
.card-data {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: 600;
  font-size: 1.2rem;
}
.card-content h3,
.card-content p {
  margin-bottom: 1rem;
}
.card-content h3 {
  font-size: 1.5rem;
}

.searchbar {
  border-color: #aaa;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.card-display {
  display: flex;
  flex-wrap: wrap;
}
.Search-filters div {
  display: inline;
  margin: 1.5rem;
}
.Search-filters label {
  margin-right: 0.5rem;
  font-size: 1.2rem;
}
.selector {
  border-color: #aaa;
  border-radius: 5px;
  padding: 0.5rem 1.5rem;
}

.App {
  text-align: center;
  font-family: 'Cormorant Upright', serif;
}
h1 {
  text-transform: uppercase;
  font-size: 3rem;
  margin-top: 2rem;
}

